.header {
   color: var(--grey-color);
   background-color: var(--black-color);

   &__container {
      @include flex-all-sb;
      padding: 14px var(--container-offset);
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
   }

   &__left {
      display: flex;
      align-items: center;
   }

   &__right {
      display: flex;
      align-items: center;
   }

   &__phone {
      display: flex;
      align-items: center;

      &-btn {
         width: 13px;
         height: 13px;
         fill: var(--grey-color);
         margin-right: 15px;
         transition: fill .3s ease-in-out;

         &:hover {
            fill: var(--white-color);
         }

         svg {

            width: 100%;
            height: 100%;
         }
      }

      &-number {
         margin-right: 30px;
         position: relative;
         color: var(--grey-color);
         transition: color .3s ease-in-out;

         &::after {
            position: absolute;
            content: "";
            width: 1px;
            height: 13px;
            top: 2px;
            right: -15px;
            background-color: var(--grey-color);
         }

         &:hover {
            color: var(--white-color);
         }
      }
   }

   &__mode {
      margin-right: 30px;
      position: relative;

      &::after {
         position: absolute;
         content: "";
         width: 1px;
         height: 13px;
         top: 2px;
         right: -15px;
         background-color: var(--grey-color);
      }
   }

   &__login {
      color: var(--grey-color);
      transition: color .3s ease-in-out;

      &:hover {
         color: var(--white-color);
      }

      &-btn {
         width: 14px;
         height: 14px;
         margin-right: 10px;
         fill: var(--grey-color);
         transition: fill .3s ease-in-out;

         &:hover {
            fill: var(--white-color);
         }

         svg {
            width: 100%;
            height: 100%;
         }
      }
   }

}